<template>
  <div>
    <v-card-title class="text-h5" style="color: #273746"> </v-card-title>
    <v-card elevation="0" outlined :style="{ backgroundColor: value === 'no' ? '#E74C3C' : '' }">

      <v-card-title>
        {{ nom }}
      </v-card-title>
      <v-card-text>
        <strong>
          <div>
            {{ description }}
          </div>
        </strong>
      </v-card-text>
      <v-divider class="mx-4"> </v-divider>
      <v-card-text>
        <v-chip-group v-model="value" active-class="primary white--text" column>
          <v-chip value="yes"> cumple </v-chip>
          <v-chip value="no"> no cumple </v-chip>
        </v-chip-group>

        <div v-if="value == 'no'">
          <br />
          <v-select outlined v-model="model" :items="items" required></v-select>
          <v-textarea
            v-model="note"
            label="Especificaciones"
            auto-grow
            outlined
            name="input-7-4"
          ></v-textarea>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "ItemBasicSelectNote",
  props: {
    template: Object,
  },
  data() {
    return {
      description: null,
      nom: null,
      value: null,
      items: null,
      model: null,
      note: null,
    };
  },
  watch: {
    value(e) {
      this.$emit("setOption", e);
    },
    model(e) {
      this.$emit("setModel", e);
    },
    note(e) {
      this.$emit("setNote", e);
    },
    template() {
      this.start();
    },
  },
  created() {
    this.start();
  },
  methods: {
    start() {
      this.description = this.template.description;
      this.nom = this.template.nom;
      this.value = this.template.value;
      this.items = this.template.items;
      this.model = this.template.model;
      this.note = this.template.note;
    },
    /*  change() {
      Soon
      this.$emit("setOption", this.value);
      Father
      @setOption="(value) => (item.value = value)"
    }, */
  },
};
</script>

<style>
.v-chip.v-size--default {
  border-radius: 20px;
  font-size: 14px;
  height: 42px;
}
</style>


